html,
body{
    background-color: teal ;
    font-family: 'Sofadi One', cursive;
  }
  .name{
      font-size: 1.7em;
      color: white;
      font-weight: bold;
      font-family: 'Sofadi One', cursive;
  
  }
  .corona-body{
    height: 100vh;
    width: 100vw;
    background-color: teal ;
  }
  .confirm{
      background-color: #e67e22 ;
      border: 1 px solid #34495e ;
    
    }
    .c-title{
  font-size: 1.5em;
  font-weight: bold; 
  color: white;
  font-family: 'Sofadi One', cursive;
  }
    .c-body{
        font-weight: bold;
    }
    
    .recovered{
      background-color: #2ecc71 ;
      border: 1 px solid #34495e;
    
    }
    .box{
        border-radius: 30px;
        box-shadow: 10px 10px 8px #04332f;
        
    }
    .card-decks{
        margin-top: 4em;
    }
    
    .deaths{
      background-color: #e74c3c ;
      border: 1 px solid #34495e;
    
    }
    
  .lastupdate{
    font-size: 0.7em;
    color: beige;
  }
  .number{
    font-size: 3em;
  }
  section{
    width: 900px;
    max-width: 900px;
    margin: 40px auto;
}

    section span{
        background: #C09A7F;
        padding: 4px;
        border-radius: 0 5px 0 0;
        color: #eee;
        font-size: 20px;
    }

    .section{
        display: table;
    }
        .section .content{
            display: table-row;
            width: 100%;
        }
        .section .box{
            display: table-cell;
            padding: 10px;
            width:31.3333%;
        }